import { groupsDetail,Addgroup,queryList } from '@/api/index'
import util from "@/util/util"
export default {
    name: 'applyEntry', // 结构名称
    data() {
        return {
            // 数据模型
            id:'',//群id
            name:'',//群名
            imgurl:'',//群头像
            num:'',//群人数
            applyTxt:'申请入群',
            mask:false,
            toastTxt:'您的申请已提交，请等待群组审批',
            showQrcode:false
        }
    },
    watch: {
        // 监控集合
    },
    props: {
        // 集成父级参数
    },
    beforeCreate() {
        // console.group('创建前状态  ===============》beforeCreate');
        this.loading();
    },
    created() {
        // console.group('创建完毕状态===============》created');
        this.id=this.$route.query.id;
        this.imgurl=this.$route.query.imgurl;
        this.num=this.$route.query.num;
        this.name=this.$route.query.name;
        if(this.$route.query.isConcern==0){
            this.showQrcode=true;
        }
        setTimeout(function(){
            groupsDetail(this.id).then(res =>{
                this.hideLoading();
                this.imgurl=res.data.imgUrl;
                this.num=res.data.userCount;
                this.name=res.data.groName;
                if(res.data.isConcern==0){
                    this.showQrcode=true;
                }
            })
        },500)
    },
    beforeMount() {
        // console.group('挂载前状态  ===============》beforeMount');
    },
    mounted() {
       
        // console.group('挂载结束状态===============》mounted');
    },
    beforeUpdate() {
        // console.group('更新前状态  ===============》beforeUpdate');
    },
    updated() {
        // console.group('更新完成状态===============》updated');
    },
    beforeDestroy() {
        // console.group('销毁前状态  ===============》beforeDestroy');
    },
    destroyed() {
        // console.group('销毁完成状态===============》destroyed');
    },
    methods: {
        // 方法 集合
        applyGroup(){
            Addgroup({groId:this.id}).then( res => {
                if(res.code == 1 ) {
                    this.applyTxt='已申请';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                        this.$router.replace({
                            path:'/nav/card'
                        })
                    }, 1500);
                }else if(res.code==403){
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else{
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }
            })
        },
        followFn(){//检测是否关注
            let appid=this.appid;
            let url=encodeURIComponent(location.href.split('?')[0].split('#')[0]+'?path=applyEntry&id='+this.id+'&istrue=1');
            util.weixinauth(appid,url);
        }   
    }

}




